import { MasterComponent } from './master.component';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';


const routes: Routes = [
    {
        path: "",
        component: MasterComponent,
        children: [
            {
                path: "",
                redirectTo: "model"
            },
            {
                path: 'category',
                loadChildren: () =>
                    import('./category/category.module').then(mo => mo.CategoryModule),
            },
            {
                path: 'model',
                loadChildren: () =>
                    import('./model/model.module').then(mo => mo.ModelModule),
            },
            {
                path: 'owner',
                loadChildren: () =>
                    import('./owner/owner.module').then(mo => mo.OwnerModule),
            },
            {
                path: 'status',
                loadChildren: () =>
                    import('./status/status.module').then(mo => mo.StatusModule),
            },
            {
                path: 'maintenance-code',
                loadChildren: () =>
                    import('./maintenance-code/maintenance-code.module').then(
                        mo => mo.MaintenanceCodeModule,
                    ),
            },
        ]
    }
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class MasterRoutingModule { }
